body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 600px) {
  body {
    background-color: rgb(230, 240, 243);
  }
  #imgborder{
    min-width: 400px;
  }
}

h1{
  color:rgb(216, 185, 84);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.containerA{
  margin: 200px;
}

.minorContainer{
  margin: 50px;
}

#imgborder img{
  border: 4px solid rgb(49, 85, 80);
  margin: 20px;
}

.square{
  min-height:60px;
  min-width:60px;
  margin:0px;
  padding:0px;
  vertical-align: top;
  font-size: 35px;
}

.menu{
  background: #f8f5f5;
}

.header{
  position: fixed;
  top: 0px;
  background: #f8f5f5;   
  display: block;
  width: 100%;
  margin-bottom:200px;
}

.containerMain{
  margin-top: 200px;
  border: 10px solid rgb(157, 157, 248);
  position: static;
  padding:10px;
  background-color: rgb(238, 246, 247);
}
#contactContainer{
  border-bottom: 2px solid rgb(193, 193, 202);
  font-weight: bold;
  text-justify:unset;
  text-align: justify;
}
#contactContainer ol{
  color:rgb(122, 122, 247);
}
h2{
  color:rgb(182, 165, 70); 
}

#message{
  padding: 10px;
  background: linear-gradient(to right, hsl(231, 75%, 54%) 0, hsl(120, 61%, 48%) 30%, hsl(56, 72%, 55%) 60%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 3s infinite linear;
  font-size: 42px;
  max-width: 400px;
  -moz-outline-radius: 15%;
  text-align: center;
}


@keyframes shine {
  0% {
    background-position: 0;
  }
  60% {
    background-position: 600px;
  }
  100% {
    background-position: 600px;
  }
}

button #headerB{
  padding:5px;
  vertical-align: bottom;
  max-height: 1px;
}